.ContactSection{
    position: relative;
    height: 100vh;
    scroll-snap-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    /*@media only screen and (max-width: 768px) {
      height: 200vh;
    }*/
  }
  
  .StarsBackground{
    z-index: 0;
    position: absolute;
    width: 100%;
    height: 100%;
  }
  
  .ContactContainer{
    position: relative;
    height: 100%;
    scroll-snap-align: center;
    width: 1400px;
    display: flex;
    justify-content: flex-end;
    /*@media only screen and (max-width: 768px) {
      width: 100%;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }*/
  }
  
  
  .ContactLeft{
    position: absolute;
    z-index: 1;
    width: 60%;
    height: 100%;
    left: 0%;;
  }
  
  .ContactRight{
    position: relative;
    z-index: 2;
    width: 50%;
    display: flex;
    align-items: center;
    /*@media only screen and (max-width: 768px) {
      flex: 1;
      align-items: center;
    }*/
  }
  
  form h1{
    color: #FFFFFF;
    font-family: 'Open Sans', sans-serif;
    font-weight: 700;
  }
  
  form{
    width: 500px;
    display: flex;
    flex-direction: column;
    gap: 25px;
  
    /*@media only screen and (max-width: 768px) {
      width: 300px;
    }*/
  }
  
  form input{
    padding: 20px;
    background-color: #e8e6e6;
    border: none;
    border-radius: 5px;
    font-family: 'Open Sans', sans-serif;
  }
  
  textArea{
    padding: 20px;
    border: none;
    border-radius: 5px;
    background-color: #e8e6e6;
    font-family: 'Open Sans', sans-serif;
  }
  
  .submitButton{
    background-color: #6B3FA0;
    color: white;
    border: none;
    font-weight: bold;
    cursor: pointer;
    border-radius: 5px;
    padding: 20px;
    font-family: 'Open Sans', sans-serif;
  }

  @media only screen and (max-width: 768px) {
    .ContactSection{
      
    }

    .StarsBackground{
      display: none;
    }

    .ContactContainer{
      width: 100%;
      justify-content: center;
      align-items: center;
    }

    .ContactLeft{
      display: none;
    }

    .ContactRight{
      width: 100%;
      display: block;
    }

    form{
      width: auto;
      padding: 10px;
    }

    textarea{
      height: 100px;
    }
  }