.WorksSection{
    height: 100vh;
    scroll-snap-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    /*@media only screen and (max-width: 768px) {
      height: 200vh;
    }*/
  }

  a{
    text-decoration: none;
  }
  
  .WorksContainer{
    position: relative;
    height: 100%;
    scroll-snap-align: center;
    width: 1400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /*@media only screen and (max-width: 768px) {
      width: 100%;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }*/
  }

  .projType{
    width: 50%;
    text-align: left;
    color: #FFFFFF;
    font-family: 'Open Sans', sans-serif;
  }

  .projCardContainer{
    width: 50%;
    height: 650px;
    position: relative;
  }

  .nextButton{
    position: absolute;
    right: -60px;
    top: 50%;
  }

  .prevButton{
    position: absolute;
    left: -60px;
    top: 50%;
  }

  .projCard{
    background-color: #452a6d;
    width: 100%;
    height: 100%;
    border-radius: 8px;
    padding: 20px;
    box-shadow: 0 0 10px 5px rgba(69, 42, 109, 0.5);
    position: absolute;
  }

  .githubLink{
    position: absolute;
    right: 0px;
    top: 0px;
    width: 70px;
    height: 70px;
  }

  .projImage{
    width: 100%;
  }

  .projCard h1{
    font-family: 'Roboto Mono', monospace;
    color: #FFFFFF;
  }

  .projCard p{
    color: lightgray;
    font-weight: 400;
    font-family: 'Open Sans', sans-serif;
    text-align: justify;
  }

  .prevButton, .nextButton{
    -webkit-appearance: none;
    background: transparent;
    border: 0;
    outline:0;
    z-index: 2;
  }
  
  .left{
    margin-right: 25px;
  }
  
  .right{
    margin-left: 25px;
  }
  
  svg {
    padding: 5px;
  }
  
  .arrow {
    cursor: pointer;
    width: 70px;
    height: 90px;
  }
  
  
  .left:hover polyline,
  .left:focus polyline {
    stroke-width: 3;
  }
  
  .left:active polyline {
    stroke-width: 6;
    transition: all 100ms ease-in-out;
  }
  
  .right:hover polyline,
  .right:focus polyline {
    stroke-width: 3;
  }
  
  .right:active polyline {
    stroke-width: 6;
    transition: all 100ms ease-in-out;
  }
  
  polyline {
    transition: all 250ms ease-in-out;
  }

  @media only screen and (max-width: 768px) {
    .WorksSection{
      width: 100vw;
      display: block;
      height: auto;
      scroll-snap-align: start;
    }

    .WorksContainer{
      width: 100%;
      display: block;
    }

    .projType{
      font-size: 24px;
      width: 100%;
    }

    .projCardContainer{
      width: 100%;
    }

    .nextButton{
      position: absolute;
      right: 0px;
      top: 50%;
    }
  
    .prevButton{
      position: absolute;
      left: 0px;
      top: 50%;
    }

    .projCard{
      width: auto;
      padding: 10px;
      height: fit-content;
    }

    .projCard h1{
      font-size: 24px;
    }

    .projCard p{
      font-size: 16px;
    }
  }